import * as nls from 'monaco-editor/esm/vs/nls.js';
import { ShowAllSymbolsActionId } from '../common/constants.js';
import { registerAction2, Action2, MenuId } from 'monaco-editor/esm/vs/platform/actions/common/actions.js';
import { IQuickInputService } from 'monaco-editor/esm/vs/platform/quickinput/common/quickInput.js';
registerAction2(class ShowAllSymbolsAction extends Action2 {
    static { this.ID = 'workbench.action.showAllSymbols'; }
    static { this.LABEL = ( nls.localize('showTriggerActions', "Go to Symbol in Workspace...")); }
    static { this.ALL_SYMBOLS_PREFIX = '#'; }
    constructor() {
        super({
            id: ShowAllSymbolsActionId,
            title: {
                value: ( nls.localize('showTriggerActions', "Go to Symbol in Workspace...")),
                original: 'Go to Symbol in Workspace...',
                mnemonicTitle: ( nls.localize(
                    { key: 'miGotoSymbolInWorkspace', comment: ['&& denotes a mnemonic'] },
                    "Go to Symbol in &&Workspace..."
                ))
            },
            f1: true,
            keybinding: {
                weight: 200 ,
                primary: 2048  | 50
            },
            menu: {
                id: MenuId.MenubarGoMenu,
                group: '3_global_nav',
                order: 2
            }
        });
    }
    async run(accessor) {
        accessor.get(IQuickInputService).quickAccess.show(ShowAllSymbolsAction.ALL_SYMBOLS_PREFIX);
    }
});
